.row.no-gutters {
  margin-right: 0;
  margin-left: 0;

  & > [class^="col-"],
  & > [class*=" col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

.overflow-hidden {
  overflow: hidden;
}

body {
  background: #ced6dc !important;
}

.btn-dark a {
  color: #fff !important;
}
a:hover {
  text-decoration: none !important;
}
.align-text-center {
  justify-content: center;
  align-items: center;
}
.link-btn {
  color: #555 !important;
}
